export default function Playlist() {
  const [currentTrack, setCurrentTrack] = useState(0);
  const [isPlay, setIsPlay] = useState(false);
  const [playlist, setPlaylist] = useState(MusicPlaylist);

  const togglePlay = () => setIsPlay(!isPlay);

  const handleNext = () => {
    setCurrentTrack((currentTrack + 1) % MusicPlaylist.length);
    setIsPlay(true);
  };

  const handlePrev = () => {
    setCurrentTrack(
      (currentTrack + MusicPlaylist.length - 1) % MusicPlaylist.length
    );
    setIsPlay(true);
  };

  const handleShuffle = () => {
    setPlaylist(shuffle(playlist));
    setIsPlay(MusicPlaylist[0]);
  };
  return (
    <Fragment>
      <div className="playlist-container">
        <div className="playlist-element" key={playlist[currentTrack].id}>
          <h1 className="glow">{playlist[currentTrack].name}</h1>
          <div className="bar">
            <img
              src={playlist[currentTrack].imageUrl}
              alt={playlist[currentTrack].imageUrl}
            />
            <ReactPlayer
              controls={true}
              width="75vw"
              height="100px"
              url={playlist[currentTrack].audio}
              playing={isPlay}
              onEnded={handleNext}
            />
          </div>
        </div>
      </div>
      <CustomPlayerBtn
        click={togglePlay}
        shuffleClick={handleShuffle}
        leftClick={handlePrev}
        rightClick={handleNext}
      />
    </Fragment>
  );
}